<template lang='pug'>
    div
        el-card
            el-steps(:active='active', finish-status='success', align-center)
                el-step(title='等待用户确认')
                el-step(title='付款中')
                el-step(title='订单完成')
            el-button(style='margin-top: 12px', @click='next') 下一步
</template>

<script>
export default {
    data () {
        return {
            active: 0
        }
    },
    methods: {
        next () {
            if (this.active++ > 2) this.active = 0
        }
    }
}
</script>

<style scoped>

</style>



